<template> 
  <div>
    <hr>
    <button @click="cosa()">Prueba</button>
    <button @click="borr()">Borra</button>
    <button @click="goToChar()">Char</button>
    <div v-for="vari in variab" v-bind:key="vari.id">
      {{vari}}

    </div>
  </div>
</template>

<script>
import {db} from '../db.js'
//import Firebase from '../db.js'

export default {
  name: 'Auth',
  data(){
    return{
      variab:[],
      marc:0,
      var:{}
    }
  },
  methods:{
    cosa:
    function(){
      db.collection('productos').add({
        tuprima:"la del pueblo",
        num:this.marc
      });
      this.marc++;
    },
    borr:
    function(){
      if(this.variab.length>0){
        this.variab.forEach(element => {
        db.collection('productos')
        .doc(element.id)
        .delete()
      });   
    }
    },
    goToChar:
    function(){
      this.$router.push("char")
    }
  },
  computed:{

  },
  firestore: {
      variab: db.collection('productos'),
  }
}
</script>

