<template> 
  <header class="header">
    <notifications group="home" position="top center" height="700"/>
    <p>Roll100</p>
    <p v-if="user.name">Usuario: {{user.name}} Email:{{user.email}}</p>
    <button @click="login()">Log in</button>
    <button @click="logout()">Log Out</button>
  </header>
</template>

<script>
import {db} from '../db.js'
import Firebase from '../db.js'
//import auth from '../db.js'


export default {
  name: 'Cabecera',
  props: {
    msg: String
  },
  mounted(){
    Firebase.auth.onAuthStateChanged( user => {
        if (user) {
          this.user.data = user;
          this.user.email = user.email;
          this.user.name = user.displayName;
        }
        else {
          this.user.data = {};
          this.user.email = null;
          this.user.name = null;
        }
      })
    
  },
  data(){
    return{
      user: {
          email: null,
          name:null,
          data: {}
      },
      variab:[],
      marc:0,
      var:{}
    }
  },
  computed:{
    
  },
  methods:{
    cosa:
      function(){
        db.collection('productos').add({
          tuprima:"la del pueblo",
          num:this.marc
        });
        this.marc++;
      },
    borr:
      function(){
        if(this.variab.length>0){
          this.variab.forEach(element => {
          db.collection('productos')
          .doc(element.id)
          .delete()
        });   
      }
    
    },
    login() {
        if(Firebase.login())
          this.$notify({
            group: 'home',
            title: 'login',
            text: 'Se ha realizado la operación con exito'
          });
      },
      logout() {
        Firebase.logout()
        this.$notify({
          group: 'home',
          title: 'logout',
          text: 'Se ha cerrado sesión con exito'
        });
      }
  },

  firestore: {
      variab: db.collection('productos'),
  },
  firebase(){

  }
}
</script>

